.Productlist{
display: flex;
flex-direction: column;
align-items: center;
background: rgb(255,255,255);
background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(235,235,235,1) 100%);
font-family: 'Rubik', sans-serif;
}
.h1-container{
	font-size: 30px;
}
p{
	font-size: 13px
}
footer{
	margin: 60px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}
.footer-p{
	font-size: 10px
}
.p-container{
	font-size: 20px;
	text-align: center;
}
@media (max-width: 600px) {
.h1-container{
	font-size: 18px;
}	
.p-container{
	font-size: 8px;
	text-align: center;
}
}