
.main-modal{
	width: 100%;
	height: 100%;
	
    background-color: white;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 100;
	display: flex;
	flex-direction: column;
	align-items: center;

	
}

.card-img-modal{
	width: 35%
}
.main-modal-title{
font-size: 30px
}
.main-modal-description{
	font-size: 20px;
	text-align: center;
}
.main-modal-button{
	width: 200px;
	padding: 9px;
	border: 2px solid #000000;
	border-radius: 25px 25px 25px 25px;
	background-color: white;
	margin: 20px;
	font-size: 25px;
	text-decoration: none;
	color:black;
	display: flex;
	justify-content: center;
}
.main-modal-button:hover{
	background-color: black;
	color:white;
}
@media (max-width: 1000px) {
.card-img-modal{
	width: 55%
}
.main-modal{
	width: 100%;
	height: 100%;

	
}
.main-modal-description{
	font-size: 15px;
	width: 90%;
}
.main-modal-button{
	width: 150px;
	
	font-size: 22px;
	
}
}

@media (max-width: 600px) {
.card-img-modal{
	width: 75%
}
.main-modal{
	width: 100%;
	height: 100%;

	
}
.main-modal-description{
	font-size: 10px;
	width: 80%;
}
.main-modal-button{
	width: 150px;
	
	font-size: 22px;
	
}
}