.main-contacts{
	display: flex;
flex-direction: column;
text-align: center;
min-height: 100vh;
background: rgb(237,237,237);
background: linear-gradient(180deg, rgba(237,237,237,1) 0%, rgba(235,235,235,1) 100%);
justify-content: center;

}
.main-contacts-title{
	font-size: 50px;
}
.main-contacts-description{
	font-size: 30px;
}

@media (max-width: 600px) {
.main-contacts-title{
	font-size: 25px;
}
.main-contacts-description{
	font-size: 10px;
}
}