.applelist-main{
background: rgb(237,237,237);
background: linear-gradient(180deg, rgba(237,237,237,1) 0%, rgba(235,235,235,1) 100%);
display: flex;
flex-direction: column;
text-align: center;
min-height: 100vh;

}
.applelist-list{
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
}