.Links{ display: flex; }
.nav-main-container{ width: 100%; 
margin-right: 10%;
margin-left: 10%;}
.nav-links{ display: flex;
	list-style: none;
	width: 100%;
	height: 100%;
	justify-content: space-between;
	align-items: center;
	}
	.nav-ul{
		display: flex;
		 list-style-type: none;
		 

	}
.nav-li-a{ color: white;
	
		 padding-right: 50px;
	      font-size: 13px; 
	      text-decoration: none;
	  }
.nav-li-a:hover{  
	             color: #AEF6FF;

	}
	
.checkout-box{
	width: 120px;
	height: 38px;
	background-color: white;
	border: 1.5px solid #000000;
	border-radius: 27px 27px 27px 27px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 18px;
	color: #000000;
	font-size: 16px; 
	font-family: 'Rubik', sans-serif;
     
}
.checkout-box:hover{
	    
	    background-color: #D9D9D9;

	}	
.checkout-item{
		
	    text-decoration: none;
	    color: #142123
	}
.logo-container{
	
	width: auto;
	height: 56px;
}
.logo-image{

	height: 100%
}
@media (max-width: 600px) {
.nav-main-container{
	width: 100%; }
	.nav-ul{
		display: flex;
		flex-direction: column;
		margin: auto;
		padding: 0;
		 margin-bottom: 20px;
	}
	.nav-li-a{ 
	
		 padding: 0px;
		 color: black;
		 font-size: 18px;

       }
       li{
       	 margin: 20px;
       }
	      
.nav-links{
	position: absolute;
	display: flex;
	justify-content: center;
	background-color: #4285F4;
	height: auto;
	width: 100%;
	flex-direction: column;
    left: 50%;
    transform: translateX(-50%);
    padding: 0px;
    margin: 0px;
    margin-top: 56px; }
.Linksclose{
	display: none;
}
.checkout-box{ margin-bottom: 10px;  }

.logo-container{
	display: none;
}
}
